import React, { Component } from 'react';
function ImageWrapper({src,title}) {
    return <span
    style={{
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center",
    }}
  >
    <img src={require(`../assets/${src}`)}></img>
    <span
      style={{
        textAlign: "center",
      }}
    >
        {title}
    </span>
  </span>;
}

export default ImageWrapper;